<template>
  <div class="dashboard truck-dashboard">
    <!-- Header Section -->
    <vx-card>
      <div class="vx-row">
        <div class="vx-col flex-wrap flex justify-between w-full">
          <div class="flex">
            <h3 class="self-center">Truck</h3>
            <div class="flex ml-4">
              <!-- <vs-button color="primary" type="border" icon="arrow_back_ios" /> -->
              <vs-input
                class="w-full mx-2"
                disabled
                :value="truckInfoHeaderLabel"
              />
              <!-- <vs-button
                color="primary"
                type="border"
                icon="arrow_forward_ios"
              /> -->
            </div>
          </div>
          <!-- <vs-input
            class="w-1/4"
            icon="search"
            icon-after="true"
            placeholder="Search for anything"
          /> -->
        </div>
      </div>
      <vs-divider />
      <div class="vx-row">
        <div class="vx-col flex-wrap flex justify-between w-full">
          <div class="flex">
            <vs-chip
              class="cursor-pointer mx-2"
              v-for="{ id, label } in chips"
              :key="id"
              :color="selectedTab === id ? 'primary' : ''"
              @click.native="changeTab(id)"
            >
              {{ label }}
            </vs-chip>
          </div>
          <span>
            <vs-button
              color="primary"
              icon="format_list_bulleted"
              @click.prevent="$router.push('/trucks')"
            >
              Trucks
            </vs-button>
          </span>
        </div>
      </div>
    </vx-card>
    <div>
      <!-- Truck Summary Card -->
      <TruckSummaryCard
        :header="truckInfoHeaderLabel"
        :isEdit="isUpdateMode"
        :enableSubmit="showSubmitButton"
        :selectedTruckTypes="selectedTruckTypes"
        @submit="submitForm"
        @onTruckTypeClick="setTruckTypes"
      />
      <section v-for="tab in chips" :key="tab.id">
        <component
          v-show="selectedTab === tab.id"
          :key="tab.id"
          :is="tab.component"
          :truckData="truckData"
          :driversList="driversList"
          ref="tabComponents"
          @cancel="redirectToTrucksList"
          @submit="submitForm"
        />
      </section>
    </div>
  </div>
</template>

<script>
// Components
import TruckSummaryCard from './components/TruckSummaryCard.vue'
import TruckOverview from './components/TruckOverview'
import FilesTab from './components/FilesTab'
import TruckMaintenance from './components/TruckMaintenance'
import TruckLogs from './components/LogsTab'
import { mapGetters } from "vuex";

export default {
  name: "TruckDashboard",
  components: {
    TruckSummaryCard,
    TruckOverview,
    FilesTab,
    TruckLogs,
    TruckMaintenance
  },
  props: ['truckDetails'],
  computed: {
    ...mapGetters('auth', ['companyFeatureAccess']),
    ...mapGetters("tag", ["allTags"]),
    truckId() {
      return this.$route.params.id || null;
    },
    truckInfoHeaderLabel() {
      if (this.isUpdateMode) {
        const { truckNumber, year, make, vin } = this.truckData || {};
        return `#${truckNumber || ""} ${year || ""} ${make || ""} ${vin || ""}`;
      }
      return "Create a truck";
    },
    isUpdateMode() {
      return this.$route.name === "UpdateTrucks";
    },
    showSubmitButton() {
      if (this.isUpdateMode) {
        return this.selectedTab === 1; // 1 : OverviewTab,
      }
      return this.selectedTab === 1 || this.selectedTab === 2; // 1 : OverviewTab, 2 : Files Tab
      // Info : Improve the property as need for later cases
    },
    chips() {
      const overViewTab = {
        id: 1,
        label: "Overview",
        component: "TruckOverview",
      };
      const filesTab = {
        id: 2,
        label: "Files",
        component: "FilesTab",
      };
      const maintenanceTab = {
        id: 3,
        label: 'Maintenance',
        component: 'TruckMaintenance'
      }
      const logsTab = {
        id: 4,
        label: "Logs",
        component: "TruckLogs",
      };
      let chipsToRender = [overViewTab, filesTab];
      if (this.isUpdateMode) {
        chipsToRender.push(maintenanceTab);
        chipsToRender.push(logsTab);
      }
      if (this.companyFeatureAccess.includes('basic')) {
        chipsToRender = [overViewTab]
      }
      return chipsToRender;
    },
    tabIndex() {
      return this.chips.reduce((result, currentValue, currentIndex) => {
        result[currentValue.label] = currentIndex;
        return result;
      }, {});
    },
  },
  async created() {
    try {
      this.$vs.loading();
      if (this.truckDetails) {
        this.truckData = { ...this.truckDetails };
      } 
      const { id } = (this.chips || [])[0] || {};
      this.changeTab(id || null);
      await this.getAllTags();
      if (this.isUpdateMode) {
        try {
          await this.getTruckDetails();
        } catch (e) {
          this.redirectToTrucksList();
        }
      } 
    } catch (e) {
      this.redirectToTrucksList();
    } finally {
      this.$vs.loading.close();
    }
    await this.getAllDrivers();
  },
  data() {
    return {
      selectedTab: null,
      // chipsList: [
        // {
        //   id: 1,
        //   label: "Overview",
        //   component: "TruckOverview",
        // },
        // {
        //   id: 2,
        //   label: "Files",
        //   component: "FilesTab",
        // },
        // {
        //   id: 3,
        //   label: 'Tolls',
        //   component: ''
        // },
        // {
        //   id: 4,
        //   label: 'Fuel',
        //   component: ''
        // },
        // {
        //   id: 5,
        //   label: 'Maintenance',
        //   component: 'TruckMaintenance'
        // },
        // {
        //   id: 6,
        //   label: 'Logs',
        //   component: 'TruckLogs'
        // },
        // {
        //   id: 7,
        //   label: 'Accidents',
        //   component: ''
        // }
      // ],
      truckData: null,
      driversList: [],
      selectedTruckTypes: [],
      dragStartFile: null,
    };
  },
  methods: {
    changeTab(id) {
      if (!id) return;
      this.selectedTab = id;
    },
    async getTruckDetails() {
      try {
        if(!this.truckId) throw new Error({ message: 'Required id missing'});
        let data = await this.$store.dispatch("trucksV2/fetchTruckInfo", this.truckId);
        const { tags: tagsData, interestRate } = data || {}
        const selectedTags = [];
        (tagsData || []).forEach((tag) => {
          const tagInfo = this.allTags.find(x => x.tag_id === tag.id);
          if (tagInfo) {
            selectedTags.push(tagInfo);
          }
        });
        this.truckData = { 
          ...data, 
          tags: selectedTags,
          interestRate: interestRate || interestRate === 0 ? parseFloat(interestRate * 100).toFixed(4): interestRate
        };
        this.selectedTruckTypes = data.truckTypes || []

        return data;
      } catch (e) {
        throw e;
      }
    },
    async submitForm() {
      const validationState = await this.validateForms();
      const overviewTabIndex = this.tabIndex["Overview"];
      const { truckPayload } = (this.$refs || {}).tabComponents[overviewTabIndex] || {};
      truckPayload.truckTypes = this.selectedTruckTypes

      if (!validationState || !truckPayload) return;
      if (this.truckId) {
        // Update flow
        this.updateTruck({
          ...truckPayload,
          id: this.truckId,
        });
      } else {
        // Create flow
        const filesTabIndex = this.tabIndex["Files"];
        const { filesPayload } = (this.$refs || {}).tabComponents[filesTabIndex] || {};
        if (filesPayload && filesPayload.length) {
          truckPayload.files = filesPayload;
        }
        this.createTruck(truckPayload);
      }

      // Reset cache
      this.$store.commit('vehicleV2/setAllVehicles', []);
    },
    async validateForms() {
      const overviewTabIndex = this.tabIndex["Overview"];
      const { validate } = (this.$refs || {}).tabComponents[overviewTabIndex] || {};
      let validationState = true;
      if (validate) {
        validationState = await this.$refs.tabComponents[overviewTabIndex].validate();
      }
      if (!validationState) {
        let text = "Correct all field errors";
        if(this.selectedTab !== 1) text += " in overview form";
        this.$vs.notify({
          color: "danger",
          title: "Error",
          text,
        });
      }
      return validationState;
    },
    async createTruck(truckPayload) {
      try {
        await this.$store.dispatch("trucksV2/createTruck", truckPayload);
        this.$vs.notify({
          color: "success",
          title: "Success",
          text: "New truck added",
        });
        this.redirectToTrucksList();
      } catch (e) {
        const { message } = ((e || {}).response || {}).data || {};
        this.$vs.notify({
          color: "danger",
          title: "Error",
          text: message || "Truck creation failed",
        });
      }
    },
    async updateTruck(truckPayload) {
      try {
        await this.$store.dispatch("trucksV2/updateTruck", truckPayload);
        this.$vs.notify({
          color: "success",
          title: "Updated",
          text: "Truck data updated",
        });
        this.redirectToTrucksList();
      } catch (e) {
        const { message } = ((e || {}).response || {}).data || {};
        this.$vs.notify({
          color: "danger",
          title: "Error",
          text: message || "Truck update failed",
        });
      }
    },
    redirectToTrucksList() {
      this.$router.push("/trucks");
    },
    async getAllTags() {
      return await this.$store.dispatch("tag/getAllTags");
    },
    async getAllDrivers() {
      try {
        const data = await this.$store.dispatch("driverV2/getAllDrivers") || {};
        this.driversList = data;
        return data;
      } catch (e) {
        throw e;
      }
    },
    setTruckTypes (truckTypes) {
      this.selectedTruckTypes = truckTypes
    }
  },
};
</script>

<style lang="scss">
.truck-dashboard {
  /* Chrome, Safari, Edge, Opera */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  /*!* Firefox *!*/
  input[type="number"] {
    -moz-appearance: textfield;
  }
  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    opacity: 1;
  }
}
</style>
